import clsx from 'clsx';
import React, { forwardRef, useContext, useEffect } from 'react';
import { TextBubble } from '@element/react-text-bubble';
import { TypoSubtitle, TypoCaption } from '@element/react-typography';
import { Icon } from '@element/react-icon';
import { StepPropTypes, StepDefaultProps } from './generated/propTypes';
import StepperContext from './StepperContext';

const getThemeColor = variant => {
    switch (variant) {
        case 'active':
            return 'primary';
        case 'completed':
            return 'primary';
        case 'incomplete':
            return 'gray';
        case 'danger':
            return 'danger';
        case 'inactive':
        default:
            return 'muted';
    }
};

const getVariant = (stepIndex, progress, nonLinear, errorState) => {
    // the current step should always be active, regardless of mode
    if (stepIndex === progress) return 'active';
    // in linear mode, anything in front the current progress should simply be inactive
    if (!nonLinear && stepIndex > progress) return 'inactive';
    // check and show the error state
    if (errorState) return errorState;
    // if there is no validation state and we're not non-linear, success!
    if (!nonLinear && stepIndex > -1 && stepIndex < progress)
        return 'completed';
    // anything we didn't catch
    return 'inactive';
};

const getBubbleText = (variant, stepNumber) => {
    switch (variant) {
        case 'completed':
            return <Icon icon='check' />;
        case 'incomplete':
            return <Icon icon='warning' />;
        case 'danger':
            return <Icon icon='error' />;
        case 'inactive':
        case 'active':
        default:
            return stepNumber;
    }
};

const getStepIndex = (stepIndexProp, stepIndexes, id) => {
    if (stepIndexProp !== undefined) return stepIndexProp;
    if (stepIndexes) return stepIndexes.findIndex(x => x === id);
    return 0;
};

const Step = forwardRef((props, externalRef) => {
    const {
        className,
        dense: denseProp,
        forceVariant,
        id,
        onClick,
        onStepEnter,
        onStepLeave,
        secondaryText,
        stepIndex: stepIndexOverride,
        tag: Tag,
        textBubbleProps,
        textBubbleText: textBubbleTextOverride,
        textBubbleThemeColor: textBubbleThemeColorOverride,
        title,
        errorState,
        variant: variantOverride,
        ...customProps
    } = props;

    const {
        dense: denseContext,
        nonLinear,
        progress,
        registerStep,
        setProgress,
        stepIndexes
    } = useContext(StepperContext) || {};

    const stepIndex = getStepIndex(stepIndexOverride, stepIndexes, id);
    const stepNumber = stepIndex > -1 ? stepIndex + 1 : '';
    const variant =
        variantOverride ||
        getVariant(stepIndex, progress, nonLinear, errorState);
    const bubbleText =
        textBubbleTextOverride || getBubbleText(variant, stepNumber);
    const bubbleThemeColor =
        textBubbleThemeColorOverride || getThemeColor(variant);
    const dense = denseProp || denseContext;
    const bubbleSize = dense ? 'small' : 'default';

    const handleClick = () => {
        setProgress(stepIndex);
        onClick?.(stepIndex);
    };

    useEffect(() => {
        registerStep?.(id || title, onStepEnter, onStepLeave);
    }, [id, title, registerStep, onStepEnter, onStepLeave]);

    return (
        <Tag
            id={id}
            ref={externalRef}
            className={clsx(
                'lmnt lmnt-stepper__step',
                `lmnt-stepper__step--${variant}`,
                className
            )}
            disabled={!nonLinear}
            onClick={handleClick}
            type='button'
            {...customProps}
        >
            <div className='lmnt-stepper__step-indicator'>
                <TextBubble
                    bubbleSize={bubbleSize}
                    themeColor={bubbleThemeColor}
                    text={bubbleText}
                    {...textBubbleProps}
                />
            </div>

            <div className='lmnt-stepper__step-text'>
                <TypoSubtitle tag='div' className='lmnt-stepper__step-title'>
                    {title}
                </TypoSubtitle>
                <TypoCaption
                    tag='div'
                    className='lmnt-stepper__step-secondary-text'
                >
                    {secondaryText}
                </TypoCaption>
            </div>
        </Tag>
    );
});

Step.displayName = 'Step';
Step.propTypes = StepPropTypes;
Step.defaultProps = StepDefaultProps;

export default Step;
