import React, { forwardRef, useContext } from 'react';
import { Divider } from '@element/react-divider';
import clsx from 'clsx';
import {
    StepDividerDefaultProps,
    StepDividerPropTypes
} from './generated/propTypes';
import StepperContext from './StepperContext';

const StepDivider = forwardRef((props, externalRef) => {
    const {
        active: activeOverride,
        className,
        dividerProps,
        nearestStepIndex,
        ...customProps
    } = props;
    const { progress, nonLinear } = useContext(StepperContext) || {};

    const active =
        activeOverride || (!nonLinear && progress > nearestStepIndex);

    return (
        <Divider
            ref={externalRef}
            variant='tall'
            className={clsx('lmnt-stepper__step-divider', className)}
            themeColor={active ? 'primary' : 'on-surface-stroke'}
            {...dividerProps}
            {...customProps}
        />
    );
});

StepDivider.displayName = 'StepDivider';
StepDivider.propTypes = StepDividerPropTypes;
StepDivider.defaultProps = StepDividerDefaultProps;

export default StepDivider;
