import clsx from 'clsx';
import React, { forwardRef, useContext } from 'react';
import {
    StepContentDefaultProps,
    StepContentPropTypes
} from './generated/propTypes';
import StepperContext from './StepperContext';

const StepContent = forwardRef((props, externalRef) => {
    // eslint-disable-next-line react/prop-types
    const {
        children,
        className,
        fullWidth,
        padding,
        persistent,
        stepId,
        tag: Tag,
        ...customProps
    } = props;
    const { stepIndexes, progress } = useContext(StepperContext) || {};
    const active = progress === stepIndexes?.findIndex(x => x === stepId);

    if (active || persistent)
        return (
            <Tag
                ref={externalRef}
                className={clsx(
                    'lmnt-stepper__step-content',
                    !active && 'lmnt-stepper__step-content--hidden',
                    fullWidth && 'lmnt-stepper__step-content--full-width',
                    padding === 'airy' && 'lmnt-padding--airy',
                    padding === 'dense' && 'lmnt-padding--dense',
                    padding === 'standard' && 'lmnt-padding--standard',
                    className
                )}
                {...customProps}
            >
                {children}
            </Tag>
        );
    return null;
});
StepContent.displayName = StepContent;
StepContent.propTypes = StepContentPropTypes;
StepContent.defaultProps = StepContentDefaultProps;

export default StepContent;
