/* eslint-disable react/prop-types */
import clsx from 'clsx';
import React, {
    forwardRef,
    useCallback,
    useEffect,
    useImperativeHandle,
    useMemo,
    useRef,
    useState
} from 'react';
import {
    StepperContainerDefaultProps,
    StepperContainerPropTypes
} from './generated/propTypes';
import StepperContext from './StepperContext';

const StepperContainer = forwardRef((props, externalRef) => {
    const {
        children,
        className,
        onConfirm,
        onNext,
        onPrev,
        onStepChange,
        tag,
        ...customProps
    } = props;
    const Tag = tag || 'div';
    const [dense, setDense] = useState();
    const [vertical, setVertical] = useState(false);
    const [nonLinear, setNonLinear] = useState(false);
    const [rtl, setRtl] = useState(false);
    const [progress, setProgress] = useState(0);
    const [stepRegistration, setStepRegistration] = useState([]);
    const [stepIndexes, setStepIndexes] = useState([]);
    const ref = useRef();

    useEffect(() => {
        setStepIndexes(stepRegistration.map(x => x.stepId));
    }, [stepRegistration]);

    const registerStep = useCallback((key, onStepEnter, onStepLeave) => {
        setStepRegistration(current => {
            if (current.find(x => x === key)) return current;
            const next = [...current];
            next.push({ stepId: key, onStepEnter, onStepLeave });
            return next;
        });
    }, []);

    const handleProgressChange = useCallback(
        newProgress => {
            setProgress(newProgress);
            stepRegistration[progress].onStepLeave?.(newProgress, progress);
            stepRegistration[newProgress].onStepEnter?.(newProgress, progress);
            onStepChange?.(newProgress, progress);
        },
        [onStepChange, progress, stepRegistration]
    );

    const prev = useCallback(() => {
        const newProgress = progress <= 0 ? 0 : progress - 1;
        handleProgressChange(newProgress, progress);
        onPrev?.(newProgress, progress);
    }, [handleProgressChange, onPrev, progress]);

    const next = useCallback(() => {
        const stepCount = stepIndexes.length;
        const newProgress = progress + 1;
        if (newProgress >= stepCount) return;
        handleProgressChange(newProgress, progress);
        onNext?.(newProgress, progress);
    }, [handleProgressChange, onNext, progress, stepIndexes.length]);

    const confirm = useCallback(() => {
        onConfirm?.();
    }, [onConfirm]);

    const contextValue = useMemo(
        () => ({
            confirm,
            dense,
            next,
            nonLinear,
            prev,
            progress,
            registerStep,
            rtl,
            setDense,
            setNonLinear,
            setProgress,
            setRtl,
            setVertical,
            stepIndexes,
            vertical
        }),
        [
            confirm,
            dense,
            next,
            nonLinear,
            prev,
            progress,
            registerStep,
            rtl,
            stepIndexes,
            vertical
        ]
    );

    useImperativeHandle(externalRef, () => ({
        ...contextValue,
        current: ref.current
    }));

    return (
        <Tag
            ref={ref}
            className={clsx(
                'lmnt-stepper__container',
                rtl && 'lmnt-stepper__container--rtl',
                vertical && 'lmnt-stepper__container--vertical',
                className
            )}
            {...customProps}
        >
            <StepperContext.Provider value={contextValue}>
                {children}
            </StepperContext.Provider>
        </Tag>
    );
});

StepperContainer.displayName = 'StepperContainer';
StepperContainer.propTypes = StepperContainerPropTypes;
StepperContainer.defaultProps = StepperContainerDefaultProps;

export default StepperContainer;
