import React, {
    forwardRef,
    Fragment,
    useContext,
    useEffect,
    useRef,
    useState
} from 'react';
import { ConditionalWrapper } from '@element/react-common';
import clsx from 'clsx';
import { StepperPropTypes, StepperDefaultProps } from './generated/propTypes';
import Step from './Step';
import StepperContext from './StepperContext';
import StepDivider from './StepDivider';
import StepperContainer from './StepperContainer';

const customContainer = containerRef => content =>
    <StepperContainer ref={containerRef}>{content}</StepperContainer>;

// TODO: Mikes todo list
// - fix flash of checked steppers on load (I think this is from the new "active" logic change)
// - finish form validation demo (do we need to add a validation to Step that passes a param that allows variant to be updated?)

const Stepper = forwardRef((props, externalRef) => {
    const {
        border,
        children,
        className,
        dense,
        nonLinear,
        progress,
        rtl,
        steps,
        tag: Tag,
        vertical,
        ...customProps
    } = props;
    const context = useContext(StepperContext);
    const [sharedState, setSharedState] = useState();
    const containerRef = useRef();

    useEffect(() => {
        // make sure we only set this once
        if (sharedState) return;
        // Shared state is managed in the StepperContainer. However, if StepperContainer has not been created externally we create one using ConditionalWrapper and get shared state via ref.
        const { setDense, setProgress, setNonLinear, setVertical, setRtl } =
            context || containerRef.current;
        if (
            !setDense ||
            !setNonLinear ||
            !setProgress ||
            !setRtl ||
            !setVertical
        )
            return;
        // store minimum we need here
        setSharedState({
            setDense,
            setNonLinear,
            setProgress,
            setRtl,
            setVertical
        });
    }, [context, sharedState]);

    useEffect(() => {
        if (dense === undefined) return;
        sharedState?.setDense?.(dense);
    }, [dense, sharedState]);

    useEffect(() => {
        if (nonLinear === undefined) return;
        sharedState?.setNonLinear?.(nonLinear);
    }, [nonLinear, sharedState]);

    useEffect(() => {
        if (progress === undefined) return;
        sharedState?.setProgress?.(progress);
    }, [progress, sharedState]);

    useEffect(() => {
        if (rtl === undefined) return;
        sharedState?.setRtl?.(rtl);
    }, [rtl, sharedState]);

    useEffect(() => {
        if (vertical === undefined) return;
        sharedState?.setVertical?.(vertical);
    }, [vertical, sharedState]);

    return (
        <ConditionalWrapper
            condition={!context}
            wrapper={customContainer(containerRef)}
        >
            <Tag
                ref={externalRef}
                className={clsx(
                    'lmnt lmnt-stepper',
                    border === 'none' && 'lmnt-stepper--un-bordered',
                    dense && 'lmnt-stepper--dense',
                    rtl && 'lmnt-stepper--rtl',
                    vertical && 'lmnt-stepper--vertical',
                    nonLinear && 'lmnt-stepper--non-linear',
                    className
                )}
                {...customProps}
            >
                {steps &&
                    steps.map((s, i) => (
                        <Fragment key={s.title}>
                            <Step stepIndex={i} {...s} />
                            {i !== steps.length - 1 && (
                                <StepDivider nearestStepIndex={i} />
                            )}
                        </Fragment>
                    ))}
                {children}
            </Tag>
        </ConditionalWrapper>
    );
});

Stepper.displayName = 'Stepper';
Stepper.propTypes = StepperPropTypes;
Stepper.defaultProps = StepperDefaultProps;

export default Stepper;
