import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    StepperButtonContainerDefaultProps,
    StepperButtonContainerPropTypes
} from './generated/propTypes';

const StepperContentContainer = forwardRef((props, externalRef) => {
    const {
        border,
        children,
        className,
        fullWidth,
        padding,
        tag: Tag,
        ...customProps
    } = props;

    return (
        <Tag
            ref={externalRef}
            className={clsx('lmnt-stepper__content-container', className)}
            {...customProps}
        >
            {children}
        </Tag>
    );
});

StepperContentContainer.displayName = 'StepperButtonContainer';
StepperContentContainer.propTypes = StepperButtonContainerPropTypes;
StepperContentContainer.defaultProps = StepperButtonContainerDefaultProps;

export default StepperContentContainer;
