import clsx from 'clsx';
import React, { forwardRef, useContext } from 'react';
import {
    StepperButtonContainerDefaultProps,
    StepperButtonContainerPropTypes
} from './generated/propTypes';
import StepperContext from './StepperContext';

const StepperButtonContainer = forwardRef((props, externalRef) => {
    const {
        border,
        children,
        className,
        fullWidth: fullWidthProp,
        padding,
        tag: Tag,
        ...customProps
    } = props;

    const { vertical } = useContext(StepperContext) || {};
    const fullWidth = fullWidthProp !== undefined ? fullWidthProp : vertical;

    return (
        <Tag
            ref={externalRef}
            className={clsx(
                'lmnt-stepper__button-container',
                border === 'none' &&
                    'lmnt-stepper__button-container--un-bordered',
                fullWidth && 'lmnt-stepper__button-container--full-width',
                className
            )}
            {...customProps}
        >
            <div
                className={clsx(
                    'lmnt-stepper__button-container-inner',
                    padding === 'dense' && 'lmnt-padding--dense',
                    padding === 'standard' && 'lmnt-padding--standard',
                    padding === 'airy' && 'lmnt-padding--airy'
                )}
            >
                {children}
            </div>
        </Tag>
    );
});

StepperButtonContainer.displayName = 'StepperButtonContainer';
StepperButtonContainer.propTypes = StepperButtonContainerPropTypes;
StepperButtonContainer.defaultProps = StepperButtonContainerDefaultProps;

export default StepperButtonContainer;
