import React, { forwardRef, useContext } from 'react';
import { Button } from '@element/react-button';
import clsx from 'clsx';
import StepperContext from './StepperContext';
import {
    StepButtonDefaultProps,
    StepButtonPropTypes
} from './generated/propTypes';

const getLabel = stepType => {
    switch (stepType) {
        case 'confirm':
            return 'Confirm';
        case 'prev':
            return 'Prev';
        case 'next':
        default:
            return 'Next';
    }
};

const StepButton = forwardRef((props, externalRef) => {
    const {
        buttonProps,
        className,
        customLabel,
        disabled,
        confirmDisabled,
        onClick,
        persistent,
        direction,
        tag: Tag = Button,
        ...customProps
    } = props;

    const { next, prev, progress, stepIndexes, confirm } =
        useContext(StepperContext) || {};

    const lastStep =
        direction === 'next' && progress === (stepIndexes?.length || 0) - 1;
    const stepType = lastStep ? 'confirm' : direction;
    const hidePrevious = stepType === 'prev' && progress === 0;
    const buttonDisabled =
        (confirmDisabled && lastStep) || disabled || hidePrevious;
    const labelActual = customLabel || getLabel(stepType);

    const handClick = () => {
        if (stepType === 'confirm') {
            confirm();
            onClick?.();
            return;
        }
        direction === 'prev' ? prev() : next();
        onClick?.();
    };

    if (!hidePrevious || persistent)
        return (
            <Tag
                ref={externalRef}
                className={clsx(
                    'lmnt-stepper__step-button',
                    hidePrevious && 'lmnt-stepper__step-button--hidden',
                    className
                )}
                disabled={buttonDisabled}
                variant={stepType === 'prev' ? 'text' : 'filled'}
                onClick={handClick}
                {...buttonProps}
                {...customProps}
            >
                {labelActual}
            </Tag>
        );
    return null;
});

StepButton.displayName = 'StepButton';
StepButton.propTypes = StepButtonPropTypes;
StepButton.defaultProps = StepButtonDefaultProps;

export default StepButton;
